.profile__photo {
  align-items: flex-start;
  border-radius: 120px;
  width: 120px;
  margin-right: 30px;
  margin-top: 18px;
  height: 120px;
  object-fit: cover;
}

.profile__photo:hover {
  opacity: .8;
}

@media screen and (max-width: 767px) {
  .profile__photo {
    margin-left: 30px;
  }
}